<template>
  <div id="summary" class="card">
    <div class="flex-center">
      Zavrna ocjena:
      <div
        id="final-grade-original"
        v-tooltip.top-start="{
          content:
            'Prosjek svih zakljunih ocjena (zavrni uspjeh).<br>' +
            'Ako ocjena iz predmeta nije zakljuena, zaokruuje se prosjek.' +
            '<br>Ako predmet nema ocjena, ne uraunava se u ovaj prosjek.',
          html: true,
        }"
      >
        {{ formatNum(finalGradeOriginal) }}
      </div>
      <transition name="opacity">
        <div
          v-if="finalGradeOriginal != finalGrade && !isNaN(finalGrade)"
          v-tooltip.top-start="{
            content:
              'Prosjek zaokruenih prosjeka.' +
              '<br>Ako predmet nema ocjena, ne uraunava se u ovaj prosjek.',
            html: true,
          }"
        >
          <div
            id="final-grade"
            :style="{
              color: finalGradeOriginal < finalGrade ? 'green' : 'red',
            }"
          >
            {{ formatNum(finalGrade) }}
          </div>
        </div>
      </transition>
    </div>
    <div id="options" class="flex-center">
      <span
        v-for="(option, optionName) in options"
        :key="optionName"
        class="material-icons flex-center"
        :class="{
          'option-enabled': option.enabled,
          'option-hidden': option.hideWhenSubjectIsOpen && isOpenedSubject,
        }"
        :style="{ 'font-size': option.fontSize || '' }"
        v-tooltip="option.tooltip"
        v-wave
        @click="$emit('optionClicked', optionName, option)"
      >
        {{ option.icon }}
      </span>
    </div>
  </div>
</template>

<script lang="ts">
import { formatNum } from "@/scripts/utils";
import { defineComponent, PropType } from "vue";
import { Option } from "./Subjects.vue";

export default defineComponent({
  name: "SubjectsSummary",
  props: {
    options: {
      type: Object as PropType<Record<string, Option>>,
      required: true,
    },
    finalGradeOriginal: Number,
    finalGrade: Number,
    isOpenedSubject: Boolean,
  },
  emits: ["optionClicked"],
  methods: {
    formatNum: (num: number): string => formatNum(num),
  },
});
</script>

<style lang="scss" scoped>
#summary {
  flex: 1 1 100%;
  display: flex;
  font-size: 24px;
  padding: 0 30px;
  margin: 0 10px 30px;

  @include themed() {
    background: linear-gradient(
        45deg,
        transparent 55%,
        t("alice-blue-light") 65%,
        t("alice-blue") calc(65% + 10px)
      ),
      linear-gradient(
        -45deg,
        t("white-background") 65%,
        t("alice-blue-light") calc(75% - 10px),
        t("alice-blue") 75%
      );
  }
}

#final-grade-original {
  padding-left: 10px;
}

#final-grade {
  padding-left: 10px;
  margin-left: 10px;
  transition: color 150ms;

  @include themed() {
    border-left: 1px solid t("light-border-color");
  }
}

#options {
  margin-left: auto;

  & > span {
    width: 24px;
    height: 24px;
    padding: 10px 10px;
    transition: background-color 150ms, opacity 150ms, color 150ms;

    &:hover {
      cursor: pointer;

      @include themed() {
        background: t("light-blue");
      }
    }
  }

  @include themed() {
    color: t("gray-blue");
  }
}

.option-enabled {
  color: $navbar-selected-text-color;
}

.option-hidden {
  opacity: 0;
  pointer-events: none;
}
</style>
